.container-upload{
   display : flex;
   flex-direction : column;
   justify-content : space-evenly;
   align-items : center;
   height : 20vh;
}

.container-upload .upload{
   width : 50%;
   height : 40%;
   background-color: #f1f2f6;
   display : flex;
   justify-content :center;
   align-items : center;
   border-radius : 5px;
   margin-bottom: 2rem;
}

.upload-show{
   height: 90vh;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
}
.upload-show .upload-btn{
   width: 45vw;
   height: 60vh;
   display: flex;
   justify-content: center;
   align-items: center;
}
.display-container{
   height :50vh;
   display : flex;
   justify-content : center;
   align-items:center;
   background-color: rgb(247, 249, 250)
}

.loading{
   position:fixed;
   background-color:#fff;
   float:left;
   top : 20vh;
   left : 0;
   width: 100vw;
   height: 20vh;
   display: flex;
   justify-content: center;
   align-items: center;
}