.home-content {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  min-height: 80vh;
  background-color: #f1f2f6;
  position: relative;
}

.home-left {
  min-width: 20%;
  margin-top: -8%;
}
.home-left h1 {
  color: rgb(69, 69, 69);
  font-size: 50px;
  font-weight: bold;
  width: 80%;
}
.home-left p{
   text-align: left;
}
.home-left p strong {
  border-bottom: 5px solid #93e3c0;
  
}
.home-right {
 
  min-width: 20%;
  background-color: #ffff;
  min-height: 300px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem
}
.container-img {
   width: 100%;
   height: 100%;
   margin-top: 0.5rem;
}
.container-img  img {
  object-fit: cover;
}

.home-right {
   display: flex;
   flex-direction:column;
   justify-content:center;
   align-items: center;
   position: relative;
}



.home-right form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width : 60%;
  text-align: center;
  /* margin-top: -0.5rem; */
  /* margin-bottom: 20px; */
}


.home-ctn-right{
   display: flex;
   flex-direction: column;
   justify-content: space-evenly;
   align-items: center;
   flex: 1 0 auto;
}

.home-btn-bottom{
   margin-top: auto;
   background-color:rgb(237, 239, 240);
   width: 100%;
   height : 30px;
   border-top: #eee;
   border-bottom-left-radius: 3px;
   border-bottom-right-radius: 3px;
   padding : 3px;
   color : rgb(116, 116, 116);

}
.home-right form button {
   display: flex;
   flex-direction: row;
   align-items: center;
   justify-content: center;
   color: #fff;
   background-color: rgb(38, 153, 251);
   outline: 0;
   border-radius:5px;
   font-weight: bold;
   font-size:20px;
   width : 13rem;
   height :3.5rem;
   border : none;
   cursor: pointer;
   margin-bottom: 5px;
}
.home-right form button i {
   font-size:30px;
   margin-right: 0.4rem;
   font-weight: bold;
}

.home-right form h2{
   color : rgb(69, 69, 69);
   font-size :2rem;
   text-align: center;
   
}

.try-img-demo{
   position : absolute;
   bottom:-40%;;
   left:0;
}
.list-imgs {
   display: flex;
   justify-content:space-around;
   align-items:center;
   margin-bottom: 3px;
  
}

.list-imgs p{
   flex-basis: 40%;
   font-size:16px;
   color : rgb(69, 69, 69);
   font-weight: 400;
  
}
.thumbnails img {
   width : 40px;
   height : 40px;
   object-fit:center;
   border-radius : 3px;
   margin-right: 2px;
   cursor: pointer;
}

.try-img-demo p:nth-child(2){
   font-size : 12px;
   color : rgb(69, 69, 69);
   text-align: center;
   /* width :100%; */
}



.home-content .home-icon{
   position : absolute;
   top : 40%;
   left : 50%;
   color : rgb(147, 227, 192);
}

.home-middle{
   display: flex;
   justify-content:center;
   flex-wrap: wrap;
   align-items:center;
   /* width: 60vw; */
   height : 60vh;
   border-bottom: 1px solid #ddd;
}
.home-middle img{
   margin-right: 4rem
}
.home-middle .home-text{
   font-size: 1.2rem;
   display: flex;
   flex-direction: column;
   justify-content: space-around;
   max-width : 30vw; 
   margin-left: 4rem;
  

}

.home-middle .home-text h2{
   font-size: 40px;
   font-weight: 700;
   /* line-height: 52px; */
   text-size-adjust : 100%;
   -webkit-tap-highlight-color: rgba(0,0,0,0);
}



.home-liveshow {
   height : 50vh;
   display: flex;
   justify-content: center;
   align-items: center;
   border-bottom: 1px solid #eee;
}
.home-demo {
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.home-demo ul {
  width: 25%;
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.home-demo .home-img-container {
  /* border : 1px solid black; */
  width: 45vw;
  height: 60vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.home-demo ul li {
  cursor: pointer;
}
.active-tab {
  color: rgb(38, 153, 251);
  border-bottom: 3px solid rgb(38, 153, 251);
}


.home-intergrate{
   border-top : 1px solid #eee;
   height:60vh;
   /* width : 50%; */
   display: flex;
   flex-direction:row;
   justify-content: center;
   align-items: center;
   color : rgb(69, 69, 69);
}

.home-intergrate-content{
   max-width: 40vh;
   height:60%;
   margin-left: 4rem;
   display: flex;
   flex-direction:column;
   justify-content: space-evenly;
   align-items: center;
}

.home-intergrate-content h2{
   font-size:2rem;
}

.home-intergrate-link{
   width : 100%;
   display: flex;
   flex-direction: row;
   justify-content : space-between;
   align-items: center;
   color : #2699fb;
}

.intergrate-btn{
   color : #2699fb;
   text-decoration: none;
   font-weight : bold;

}

.home-bottom{
   border-top : 1px solid #eee;
   border-bottom : 1px solid #eee;
   min-height: 30vh;
   width : 100vw;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
}
.home-bottom h2,p{
   /* width : 40%; */
   text-align: center;
}
.home-bottom-link{
   display: flex;
   flex-direction:column;
   flex-wrap: wrap;
   /* flex */
}

.home-bottom-link div{
   display: flex;
   flex-direction:row;
   flex-wrap: wrap;
   justify-content:space-evenly;
   align-items: center;
   width : 50vw;
   /* text-align: center; */

   /* flex */
}

.home-bottom-link-btn{
   color : #2699fb;
   text-decoration: none;
   font-weight : bold;
   text-align: left;

}