.display{
   background-color: #fff;
   width : 50vw;
   height : 80%;
}

.display-title {
   list-style: none;
   display: flex;
   flex-direction: row;
   justify-content: space-between;
   
}
.display-nav{
   display: flex;
   flex-direction: row;
   justify-content: space-between;
   width : 30%;
   margin-top:0.3rem;
   margin-left: 0.4rem;

}

.icon{
   margin-top: 0.3rem;
   margin-right: 0.6rem;
   color : #ccc;
   font-size:1.2rem;
   cursor: pointer;
}

.display-nav div{
   cursor: pointer;

}

.display-img {
   display: flex;
   justify-content: space-between;

   align-items: center;
}


.display-img .ctn-img{
   width: 66%;
   /* flex-basis: 66%;  */
   /* width: 66%; */
   /* object-fit: cover; */
   /* height: 60%; */
   
} 
.ctn-img img{
   margin-left: 1rem;
   margin-top: 1rem;
   width:80%;
   height:80%;
   object-fit:cover;
   background-image : url(../assets/images/background.png);
}
.display-img .display-download{
   flex-basis: 33%;
   display: flex;
   flex-direction: column
}

.active-display {
   color: rgb(38, 153, 251);
   border-bottom: 3px solid rgb(38, 153, 251);
 }
