.header-container {
  /* position:fixed */
  display: flex;
  justify-content: space-between;
  min-height: 50px;
  background-color: #fff;
  align-items: center;
  flex-wrap: wrap;
  /* position :fixed; */
}

.header-container .header-left ul {
  list-style-type: none;
  min-width: 35vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
}
.header-container .header-left ul li {
  margin-right: auto;
}

.header-container .header-left ul li:hover {
  border-bottom: 1px solid rgb(84, 97, 108, 0.5);
}

.header-container .header-left ul li:nth-child(1):hover {
  border-bottom: none !important;
}
.header-container .header-left ul li img {
  cursor: pointer;
  min-width: 150px;
  height: 25px;
  margin-left: 1rem;
}

.header-container .header-right {
  list-style-type: none;
  width: 23vw;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 6vh;
  flex-wrap: wrap;
}
.header-right button {
  height: 60%;
  display: block;
  padding: 0.25rem;
  cursor: pointer;
  margin: auto;
  font-weight: 500;
  font-size: 16px;
  /* letter-spacing:0.5px; */
}
.header-right button:nth-child(1) {
  border: 1px solid rgb(38, 153, 251);
  min-width: 45%;
  border-radius: 5px;
  color: rgb(38, 153, 251);
}

.header-right button:nth-child(2) {
  border: 1px solid rgb(84, 97, 108);
  border-radius: 5px;
  min-width: 35%;
}
.header-right button:nth-child(1):hover {
  color: #fff;
  background-color: rgb(38, 153, 251);
}

.header-right button:nth-child(2):hover {
  color: #fff;
  background-color: rgb(84, 97, 108);
}

