.container-tool {
  background-color: rgb(69, 69, 69);
  padding: 20px;
}

.container-tool ul {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  list-style: none;
  width: 80%;
  margin: 0 auto;
  color: #fff;
}
.container-tool ul li div {
  display: flex;
  justify-content: center;
  align-items: center;
}
.container-tool ul li p {
  font-size: 15px;
}

.container-tool ul li div img {
  width: 40px;
  height: 40px;
  display: inline-block;
  margin: auto;
  cursor: pointer;
}

.container-tool ul li:first-child {
  font-size: 20px;
  font-weight: bold;
}

.container-tool ul li:last-child {
  font-size: 20px;
  font-weight: bold;
  background-color: #fff;
}
.container-tool ul li:last-child i {
  color: rgb(69, 69, 69);
  background-color: #fff;
}

.container-tool ul li:last-child input {
  outline: none;
}

.container-navSlide {
  width: 60%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  position: relative;
  /* background-color: rgb(249, 250, 250); */
  min-height: 400px;
  transition: all 0.3;
  flex-wrap: wrap;
}

.container-navSlide i {
  position: absolute;
  cursor: pointer;
  font-size:20px;
}
.container-navSlide > div {
  /* border : 1px solid black; */
  background-color: #fff;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  max-width: 45%;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
   border-radius: 5px;
  max-height : 200px;
  padding : 10px;
  position: relative;
  /* overflow: hidden; */
}
.container-navSlide > div:before{
   position: absolute;
   content: "OFFICAL";
   top : -10px;
   left : 45%;
   box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
   background-color: #98a0a6;
   border-radius:3px;
   color : #fff;
   transition: all 0.3;

   
}

container-navSlide div div:nth-child(1) {
  max-width: 20%;
  background-size: "cover";
  margin: auto;
}
.container-navSlide img {
  object-fit: contain;
  width: 80%;
  height: 80%;
  margin: auto;
  cursor: pointer;
}

.container-navSlide div div:nth-child(2) {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  min-width: 70%;
  flex: 1;
}

.container-navSlide .fa-arrow-right {
  position: absolute;
  top: 50%;
  left: 105%;
}

.container-navSlide .fa-arrow-left {
  position: absolute;
  top: 50%;
  left: -5%;
}

.container-design{
   width : 60%;
   display: flex;
   flex-direction: row;
   flex-wrap: wrap;
   justify-content:space-between;
   align-items:center;
   margin : 1rem auto;
}
.container-design img{
   object-fit:contain;
   width : 100%;
   height :100%;
}

.container-design  div{
   width : 250px;
   height : 250px;
   margin-bottom: "2px";
   display : flex;
   flex-direction: column;
   justify-content:space-evenly;
   align-items: center;
   padding : 5px;
   border-radius:5px;
   cursor: pointer;
}
