.container-liveshow {
   display: flex;
   flex-direction: column;
   justify-content:center;
   align-items:center;
}

.container-liveshow ul{
   list-style-type:none;
   width : 20vw;
   display: flex;
   justify-content:space-between;
   align-items:center;
   margin-bottom: 1.5rem;

}

.container-liveshow ul li{
   cursor: pointer;
   

}
.conatiner-img-live{
   width :50vw;
   display: flex;
   flex-direction: row;
   justify-content:space-between;
   align-items:center;
   flex-wrap: wrap;
}
.ctn-img-1,.ctn-img-2{
   position: relative;
}

.ctn-img-1 img:nth-child(2){
   position: absolute;
   top : 50%;
   left : 75%;
   background-image: url(../assets/images/background.png);
}

.ctn-img-1 svg{
   position: absolute;
   top : 100%;
   left : 30%;
   width : 50px; 
   height : 50px;
   color :rgb(84, 97, 108);
}

.cnt-img-middle{
   position: relative;
}
.cnt-img-middle svg{
   position: absolute;
   top : 10%;
   left : -30%;
   width : 50px; 
   height : 50px;
   color :rgb(84, 97, 108);
   transform : rotate(180deg) scaleX(-1);
}
.ctn-img-2 img:nth-child(1){
   position: absolute;
   top : 50%;
   left : -75%;
}

.active-live {
   color: rgb(38, 153, 251);
   border-bottom: 3px solid rgb(38, 153, 251);
 }
 