.img-container{
   display : flex;
   width : 100%;
   height : 100%;
   justify-content :center;
   align-items :center;
   background : #f3fbff;

}

.img-box{
   position: relative;
   width : 90%;
   margin: auto;
   line-height:0;
   background-image : url(../assets/images/background.png);
   overflow: hidden;

}
.img-wrap{
   overflow: hidden;
   position: absolute;
   top: 0;
   left: 0;
   /* width : 20px; */
}

.img-box span {
   width : 1px;
   height : 100%;
   cursor: ew-resize;
   background: #fff;
   position: absolute;
   left: 50%;
   top: 0;
}

.img-box span .img-arr{
   width :40px;
   position: absolute;
   top : 50%;
   margin-left: -20px;
   margin-top: -20px;
}